import { useRemoteMedia } from './useRemoteMedia';

export const useRemoteAudio = (props: {
  peerId: string;
  onClose?: (reason?: {
    code: number;
    tag: string;
    message: string;
  }) => void;
  onPlayable?: (data: {
    track: MediaStreamTrack;
    stream: MediaStream;
    label: 'audio';
  }) => void;
}) => {
  const { state, track, stream } = useRemoteMedia({
    label: 'audio',
    peerId: props.peerId,
    onStopped: props.onClose,
    onPlayable: (data) => {
      props.onPlayable?.({
        label: 'audio',
        track: data.track,
        stream: data.stream,
      });
    },
  });

  const isAudioOn = stream ? true : false;

  return {
    state,
    track,
    stream,
    isAudioOn,
  };
};
